import React from 'react';
import './Shopper.css'
import { DatePicker, Checkbox } from 'antd'
import moment from 'moment'

const { RangePicker } = DatePicker

export default class extends React.Component {
    state = {
        checkedItems: []
    }
    componentDidMount() {
        this.setState({
            dates: window.foodnet.plannerDates && window.foodnet.plannerDates.length === 2 ? window.foodnet.plannerDates.map(it => moment(it)) : undefined,
            checkedItems: window.foodnet.plannerCheckedItems || [],
        })
    }
    render() {
        //TODO: sync checkedItems (on date change also)

        return (
            <div className="shopper">
                <RangePicker value={this.state.dates} onChange={(m) => {
                    this.setState({ dates: m, checkedItems: [] })
                    window.foodnet.plannerDates = m.map(it => it.format());
                    window.foodnet.plannerCheckedItems = [];
                    window.foodnet.sync();
                }} />
                {this.state.dates && (
                    this.createIngredientList()
                )}
            </div>
        );
    }

    createIngredientList() {
        const { foodnet } = window;
        let list = [];
        foodnet.events.forEach(event => {
            if (moment(event.date).isBetween(...this.state.dates, 'days', '[]')) {
                const multiplier = Number(event.qty) || 0;
                const recipe = foodnet.recipes.find(r => r.id === event.recipe);
                recipe.ingredients.forEach(ing => {
                    list.push({ id: ing.id, type: ing.type, qty: Number(ing.qty) * multiplier });
                })
            }
        })

        const mergeDuplicates = (item, index, l) => {
            if (index > 0 && l[index - 1].id === item.id && l[index - 1].type === item.type) {
                let newIndex = index - 1;
                while (l[newIndex].deleted) newIndex--;
                l[newIndex].qty += item.qty;
                item.deleted = true;
            }
            return item;
        };
        list = list.sort((a, b) => {
            if (a.id !== b.id) {
                return a.id < b.id ? -1 : 1;
            }
            if (a.type !== b.type) {
                return a.type < b.type ? -1 : 1;
            }
            return 0;
        }).map(mergeDuplicates).filter(item => !item.deleted);

        return (
            <ul className="ingredient-list">
                {list
                    .map(item => ({
                        ...item,
                        name: foodnet.ingredients.find(i => i.id === item.id).name,
                        typename: foodnet.measurements.find(m => m.id === item.type).name,
                        checked: this.state.checkedItems.indexOf(item.id) !== -1,
                    }))
                    .sort((a, b) => {
                        if (a.name.toLowerCase() !== b.name.toLowerCase()) {
                            return a.name.toLowerCase() < b.name.toLowerCase() ? -1 : 1;
                        }
                        if (a.typename.toLowerCase() !== b.typename.toLowerCase()) {
                            return a.typename.toLowerCase() < b.typename.toLowerCase() ? -1 : 1;
                        }
                        return 0;
                    })
                    .map((item, index) => (
                        <li key={index}>
                            <Checkbox key={index} checked={item.checked} onChange={e => {
                                foodnet.plannerCheckedItems = foodnet.plannerCheckedItems.filter(it => it !== item.id)
                                if (e.target.checked) {
                                    foodnet.plannerCheckedItems = foodnet.plannerCheckedItems.concat(item.id);
                                }
                                this.setState({ checkedItems: foodnet.plannerCheckedItems });
                                foodnet.sync();
                            }}>
                                {`${item.name}: ${item.qty} ${item.typename}`}
                            </Checkbox>
                        </li>
                    ))}
            </ul>
        );
    }
}